<template>
    <div>
		<h1>My work</h1>
		<b>
			<p>
				I am currently emloyed at HelliT.eu
			</p>
		</b>
		<p>
			I have worked on various projects from NASA's space program to a cyber attack on a Mars alien base
		</p>
		<div id="works">
			<div class="work" v-for="item in items">
				<h3>{{title}}</h3>
				<p>{{description}}</p>
			</div>
		</div>
    </div>
</template>

<script>
	
    export default {
        name: 'Next',
        data () {
            return {
            	items: JSON.parse(require("../works.json"))
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1, h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
</style>



